const inputs = Array.from(document.querySelectorAll('.text-input'));

if (inputs[0] !== null) {
    inputs.map(input => {
        input.addEventListener('focusout', function(e) {
            if (this.value !== '') {
                this.nextElementSibling.classList.add('has-value');
            } else {
                this.nextElementSibling.classList.remove('has-value');
            }
        });
    });
}
