import Swiper from 'swiper';
import cdnLoad from './cdnLoad';

const carousel = document.getElementById('main-banner');

if (carousel !== null) {
    cdnLoad();

    new Swiper('.main-banner-swiper', {
        slidesPerView: 1,
        centeredSlides: true,
        loop: true,
        autoplay: {
            delay: 3000,
            disableOnInteraction: false,
        },
        pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
        },
        navigation: {
            nextEl: '.carousel-button-next',
            prevEl: '.carousel-button-prev',
        },
        breakpoints: {
            768: {
                slidesPerView: 'auto',
                loopFillGroupWithBlank: true,
            },
        },
    });

    new Swiper('.new-products-swiper', {
        slidesPerView: 5,
        slidesPerGroup: 5,
        loop: true,
        loopFillGroupWithBlank: true,
        navigation: {
            nextEl: '.newproducts-button-next',
            prevEl: '.newproducts-button-prev',
        },
    });

    new Swiper('.cosmetics', {
        slidesPerView: 5,
        slidesPerGroup: 5,
        loop: true,
        loopFillGroupWithBlank: true,
        navigation: {
            nextEl: '.cosmetics-button-next',
            prevEl: '.cosmetics-button-prev',
        },
    });

    new Swiper('.health', {
        slidesPerView: 5,
        slidesPerGroup: 5,
        loop: true,
        loopFillGroupWithBlank: true,
        navigation: {
            nextEl: '.health-button-next',
            prevEl: '.health-button-prev',
        },
    });
}
